import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class RegisterPending extends Component {
    render() {
        return (
            <div className="text-center pending-wrapper">
                <h3 className="ns-B">    .</h3>
                <h5>     <span className="point-red">   </span>.</h5>
                <h5><span className="point-red">2 </span>    <span className="point-red"></span> .</h5>
                <div className="padding-top50">
                    <div className="btn-container">
                        <Link to="/" className="btn btn-common btn-next"> </Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default RegisterPending;


